import React, {Fragment} from "react";
import Barber from '../../../images/barber.jpg';
import profile from '../../../images/profile.webp'
import { useEffect } from "react";
import { connect } from "react-redux";
import { logout, load_user } from "../../../actions/auth";

import { Wrapper, Content, BarberLogo, Profile, HeaderText, ArrowDown, ProfileContent } from "./Header.styles";
import { Link, Navigate } from "react-router-dom";

const Header = (logout, isAuthenticated, load_user) => {
    const guestLinks = () =>(
        <Fragment>
            <li>
                <Link to=''>Login</Link>
            </li>
        </Fragment>
    )
    const authLinks = () =>(
        <a onClick={logout}>Logout</a>
    );

    const logoutHandler =() =>{
        logout();
    }
    function Logout(){
        localStorage.removeItem('access');
        localStorage.removeItem('refresh');
        window.location.pathname=''
    }
    return(
        <Wrapper>
        <Content>
            <BarberLogo src={Barber} all='barber'/>
            
            <h2>Barbershop booking appointment</h2>
                
           <ProfileContent>
           <HeaderText>Profile Name</HeaderText>
            <Profile src={profile} all='profile'/>
            <div className="dropdown">
            <ArrowDown/>
            <div className="dropdown-content">
            <a>Profile</a>
            <a>My Booking</a>
            <a onClick={Logout}>Logout</a>
            </div>
            </div>
           
             
               
           </ProfileContent>
           <div className="arrow">
           <div className="ArrowDown"></div> 
           </div>
           
        </Content>
    </Wrapper>
    );
    
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
})

export default connect(mapStateToProps,{logout})(Header);